.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #0e1111;
  height: 110vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: #fff9ef;
  gap: 5rem;
}
.mainContent{
  width: 40vw;
}
p {
  color: #fff9ef;
  font-size: 20px;
}
.signUp {
  font-size: 25px;
  background-color: #0e1111;
  color: #fff9ef;
  border: 1px solid #fff9ef;
  border-radius: 10px;
  height: 75px;
  width: 200px;
}
#form {
  background-color: transparent;
}
form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40vw;
  gap: 1rem;
}
label {
  align-self: flex-start;
}
input {
  height:2rem;
  width: 40vw;
  border-radius: 15px;
  border-style: none;
}
button {
  width: 20%;
  background-color: transparent;
  font-weight: 900;
  height: 2rem;
  border-radius: 15px;
  border-style: none;
  font-size: 1rem;
  color: #fff9ef;
  border: 1px solid #fff9ef;
}
.policy {
  font-size: 1rem;
  width: 90%;
}